@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;600&family=Poppins:wght@300;500;900&display=swap);
html,body {
    overflow: hidden;
}
body {
    padding:90px;
    background-color:#1B2021;
    color:rgb(235, 235, 235);

    font-family: Poppins, Arial, Helvetica, sans-serif;
}

#app {
    display:flex;
    justify-content: center;
}

div {
    display:block;
}

#footer {
    position:fixed;
    left:0;
    bottom:0;
    width:100%;
    background-color:#30343F;
    text-align: center;
    margin:0;
    padding:0;
    box-shadow: 0 -6px 12px rgba(0, 0, 0, 0.5);
}
#footer h1 {
    font-size: medium;
    margin:10px 0;
    padding:0;
    color:#4d566d;
}
#footer #social-links {
    list-style: none;
    margin: 14px 0;
    padding:0;
    text-align: center;
}
#footer #social-links li {
    display:inline;
    margin:0 20px;
    border-radius: 50%;
    font-size: large;
    padding:2px 7px;
    -webkit-transform-origin: center;
            transform-origin: center;
}
#footer #tw-icon {
    background-color:rgb(0, 168, 247);
}
#footer #gh-icon {
    background-color:#8700e0;
}
#footer #gl-icon {
    background-color:rgb(255, 0, 191);
}
#footer i {
    color:white;
    transition: all 0.5s ease-out;
}
#footer #social-links li:hover {
    cursor: pointer;
}
#footer #social-links li:hover i {
    -webkit-transform:scale(1.2,1.2) rotate(360deg);
            transform:scale(1.2,1.2) rotate(360deg);
}



.header form {
    text-align: center;
}
.header input {
    width:300px;
    font-size: large;
    border-radius: 0 0 0 15px;
    outline: 0;
    border: 0;
    margin:0;
    padding: 3px 8px;
    font-family: Poppins, Arial, Helvetica, sans-serif;
}
.header button {
    font-size: medium;
    padding:6px 12px;
    font-family: Poppins, Arial, Helvetica, sans-serif;
    margin-bottom:4px;
    font-weight:normal;
    font-size: 14px;
    vertical-align: middle;
    -webkit-transform-origin: center;
            transform-origin: center;
    border-radius: 0 0 15px 0;
    outline: 0;
    border:0;
    background-color: #EA638C;
    transition: all 0.5s ease-out;
    color:rgb(26, 26, 26);
}
.header button:hover {
    background-color:#ff5789;
    box-shadow: 0 4px #89023E;
    -webkit-transform:translateY(-2px);
            transform:translateY(-2px);
    cursor: pointer;
}

.theList {
    list-style: none;
    text-align: left;
    margin:0;
    padding:0;
}
.theList li {
    margin:20px 0;
    padding:8px;
    background-color: #EA638C;
    border-radius: 0 15px 0 15px;
    width:350px;
    position: relative;
    transition: all 0.5s ease-out;
}
.theList li:hover {
    background-color:blue;
    cursor: pointer;
    -webkit-transform:translateY(-2px);
            transform:translateY(-2px);
    box-shadow: 0 4px rgb(0, 0, 180);
}
#list-item h3 {
    background-color: #EA638C;
    display:inline;
    position:absolute;
    -webkit-transform:translate(-100px, -12px);
            transform:translate(-100px, -12px);
    font-size:medium;
    padding:4px;
    border-radius: 15px;
    font-weight: lighter;
}
